import { Injectable } from '@angular/core';
import { ActivatedRouteSnapshot, CanActivate, Router, RouterStateSnapshot, UrlTree } from '@angular/router';
import { AuthService } from '@src/app/services/auth/auth.service';
import { Observable } from 'rxjs';

@Injectable({
    providedIn: 'root'
})
/**
 * Der RoleGuardService ist ein Guard, also fr die Navigation der Route innerhalb der Anwendung zustndig.
 * Dieser Guard ist fr die Identifikation der Rolle des Nutzers zustndig (CREATOR, PARTICIPANT, ADMIN).
 * Dazu verwendet es das CanActivate Interface, welches von jedem anderen Guard verwendet wird.
 * Nur wenn alle Guards wahr sind, kann auf die gewnschte Ansicht navigiert werden, sonst nicht.
 */
export class RoleGuardService implements CanActivate {

    /**
     * Konstruktor des RoleGuardService. Darin werden smtliche Services initialisert,
     * die fr den RoleGuardService gebraucht werden.
     * @param authService AuthService Singleton, ist fr das Aufrufen des registrierten Nutzers zustndig
     * @param router Router Singleton, zum Routen des Pfads nach Eintritt eines Ereignisses
     */
    constructor(private router: Router, private authService: AuthService) { }

    /**
     * Die vom Interface zu implementierende Methode, welche den Guard aktiviert oder deaktiviert.
     * @param route ActivatedRouteSnapshot ist der aktuellen Zustand der aktivierten Route 
     * @param state RouterSnapshot ist der aktuelle Zustand des Routers
     * @returns Promise<boolean>, je nachdem ob der Nutzer die bentigte Rolle fr das Aufrufen der Ansicht besitzt oder nicht.
     */
    canActivate(route: ActivatedRouteSnapshot, state: RouterStateSnapshot)
    : boolean | UrlTree | Observable<boolean | UrlTree> | Promise<boolean | UrlTree> {
        if (!this.authService.getUser().permission >= route.data.expectedRole) {
            this.router.navigate(["login"], { replaceUrl: true });
            return false;
        }
        return true;
    }
}
