import { Component, OnInit } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { AuthService } from '../services/auth/auth.service';
import { StudyWrapperService } from '../services/study-wrapper/study-wrapper.service';

@Component({
    selector: 'app-dashboard',
    templateUrl: './dashboard.component.html',
    styleUrls: ['./dashboard.component.css']
})
/**
 * Die DashboardComponent enthlt die Seitenansicht in der Anwendung.
 * Darin sind alle Dienste festgehalten, die invariant zu jeder anderen Sicht (auer der Studienerstellung und -teilnahme) sind.
 */
export class DashboardComponent implements OnInit {
    /**
     * Konstruktor der AvailableStudiesComponent. Darin werden smtliche Services initialisert,
     * die fr die "Verfgbare Studien"-Ansicht gebraucht werden.
     * @param authService AuthService Singleton, ist fr die Authentifizierung des Nutzers zustndig
     * @param route ActivatedRoute ist die aktivierte Route
     */
    constructor(private studywrapper: StudyWrapperService, public authService: AuthService, public route: ActivatedRoute) { }

    /**
     * Initialisiert die DashboardComponent.
     */
    async ngOnInit() {
        this.studywrapper.study = undefined;
    }

    /**
     * Loggt den Nutzer aus der Anwendung aus und navigiert ihn zurck zur Login-Ansicht.
     */
    logout() {
        this.authService.logout();
    }
}
