export enum StudyStatus {
    /**
     * Die Studie wurde erstellt und ist noch in Bearbeitung also nicht verffentlicht
     */
    CREATED,

    /**
     * Die Studie wurde verffentlicht und Studienteilnehmer knnen an der Studie teilnehmen
     */
    PUBLISHED,

    /**
     * Es ist nicht mehr mglich an der Studie teilzunehmen.
     * Der Studienleiter kann nun die Ergebnisse auswerten
     */
    FINISHED
}
